<template>
    <div id="foldLineChart">

    </div>
</template>

<script>
let echarts = require('echarts');
//import * as dsv from "d3-dsv";
//let axios = require("axios");
// import  'echarts/theme/forest.js'
export default {
    name:"CityAdded",
    data(){
        return{
            date: [],
            diagnosis: [],
            cure: [],
            death: []
        };
    },
    methods:{
        drawLine(){
            //let myChart = this.myChart
            // this.$echarts.registerTheme('walden',walden)
            var chart = echarts.init(document.getElementById('foldLineChart'));
            var option= {
                color: [
          "#FF0E0E",
          "#E7E8D1",
          "#57B37D",
          "#8DC1A9",
          "#EA7E53",
          "#EEDD78",
          "#73A373",
          "#759AA0",
          "#7289AB",
          "#91CA8C",
          "#F49F42",
          "#FFFFFF"
        ],
        textStyle: {
          color: "#ffffff",
          fontSize: "15"
        },
                title: {
                    text: '成都市新增趋势',
                    textStyle:{
                        color: "#ffffff",
                        fontSize: "23"
                    },
                    left: "5.1%"
                },
                legend: {
                    data: ["确诊", "治愈", "死亡"],
                    textStyle: {
                        color: "#ffffff"
                    },
                    right: "9.5%",
                    top: "2%"
                },
                tooltip: {
                    trigger: "axis"
                },
                xAxis: {
                    type: "category", 
                    name: "时间",
                    data: this.date
                },
                yAxis: {
                    type: "value"
                },
                dataZoom: [
            {
                show: true,
                realtime: true,
                // start: 30,
                // end: 70,
                //xAxisIndex: [0, 1]
            },
        
            ],
                series: [
                    {
                        smooth: true,
                        name: "确诊",
                        type: "line",
                        data: this.diagnosis
                    },
                    {
                        smooth: true,
                        name: "治愈",
                        type: "line",
                        data: this.cure
                    },
                    {
                        smooth: true,
                        name: "死亡",
                        type: "line",
                        data: this.death
                    }
                ]




            };
            chart.setOption(option,true)
        }

    },
    mounted(){
        this.axios.get('api/users/chengdu').then((res)=>{
            // console.log(res.data[0].确诊病例);
                for (var i=0; i<res.data.length; i++){
                    this.date.push(res.data[i].统计时间);
                    this.diagnosis.push(res.data[i].确诊病例);
                    this.cure.push(res.data[i].治愈);
                    this.death.push(res.data[i].死亡);
                    //  this.diagnosisDataset.push(res.data[i].统计时间, res.data[i].确诊病例);
                }
                console.log(this.date);
                console.log(this.diagnosis);
                this.drawLine()
        })
    }
}
</script>

<style>
#foldLineChart{
    position: absolute;
    width: 90%;
    height: 90%;
    top: 5%;
    left: 5%;
    /* background-color: #30313a; */
    
}
</style>