<template>
    <div id="barChart">
    
    </div>
</template>

<script>
let echarts = require('echarts');
// import  'echarts/theme/carp.js'
export default {
    name : "CityRanked",
    data(){
        return{
            city_level: [],
            country_level: []

        };
    },
     mounted(){
         this.drawBar();
        
    },
    methods:{
        drawBar(){
                //let myChart = this.myChart
                var chart = echarts.init(document.getElementById('barChart'));
                //chart = this.chart;
                var option= {
                    textStyle: {
                        color: "#ffffff",
                        fontSize: "15"
                    },
                    title:{
                        text: "医疗资源TOP5",
                        textStyle:{
                        color: "#ffffff",
                        fontSize: "23"
                        },
                        left: "2%"
                    } ,
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: {            // 坐标轴指示器，坐标轴触发有效
                        type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                        }
                    },
                    legend:{
                        data: ['市级医院', '县级医院'],
                        textStyle: {
                            color: "#FFF"
                        },
                        right: "5%"
                    },
                    grid:{
                        bottom: "1%",
                        top: "10%",
                        left: "8%",
                        containLabel: "false"
                    },
                    xAxis:{
                        show: false
                    },
                    yAxis:{
                        type: 'category',
                        data: ['乐山', '宜宾', '阿坝', '凉山', '甘孜', '成都']
                    },
                    series:[
                        {
                            name: '县级医院',
                            type:  'bar',
                            stack: '总量',
                            itemStyle: {
                                color: '#1c79c0'
                            },
                            data: [10, 10, 12, 17, 20, 28]
                        },
                        {
                            name: '市级医院',
                            type: 'bar',
                            stack: '总量',
                            // barCategoryGap: '35%',
                            itemStyle: {
                                color: '#71DBF9'
                            },
                            data: [1, 1, 1, 2, 3, 7]
                        }
                        
                    ]

                };
                chart.setOption(option,true)
        },
    },

   


}
</script>

<style>
#barChart{
    position: absolute;
    width: 90%;
    height: 90%;
    top: 5%;
    left: 5%;
}
</style>